function rot47(input) {
  return input.split('').map(function (i) {
    return i.charCodeAt(0);
  }).map(function (i) {
    return i + 47;
  }).map(function (i) {
    return i % 127;
  }).map(function (i) {
    return i < 80 ? i + 33 : i;
  }).map(function (i) {
    return String.fromCharCode(i);
  }).join('');
}
module.exports = rot47;