function rot5(input) {
  return input.split('').map(function (i) {
    return i.charCodeAt(0);
  }).map(function (d) {
    return d >= 48 && d <= 57 ? d + 5 > 57 ? d - 5 : d + 5 : d;
  }).map(function (d) {
    return String.fromCharCode(d);
  }).reduce(function (a, c) {
    return a + c;
  }, '');
}
module.exports = rot5;